import { useCallback, useEffect, useState } from "react";
import SelectReturnGoodsCard from "./SelectReturnGoodsCard";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useStoreAllGoodsList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../PlateformUser/PlateformUser.module.css";

const SelectReturnGoods = ({ onClose, stid, dataState, onGoodsSelect }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [goodsList, setGoodsList] = useState({
    goods: [],
    goodsId: [],
  });

  const { storeAllGoodsList, storeAllGoodsListLoading } = useStoreAllGoodsList({
    data: {
      stid: stid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !stid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeAllGoodsList?.list?.length) {
      let obj = nextPage(storeAllGoodsList?.list?.length);
      setState(() => obj);
    }
  }, [storeAllGoodsList?.list?.length]);

  useEffect(() => {
    if (dataState?.goodsId?.length) {
      setGoodsList({
        goods: dataState?.goods,
        goodsId: dataState?.goodsId,
      });
    }
  }, [dataState?.goodsId]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onNext = () => {
    onGoodsSelect(goodsList);
  };

  const onSelectGoods = (val) => {
    if (goodsList.goodsId?.includes(val?._id)) {
      setGoodsList((prev) => ({
        ...prev,
        goods: prev?.goods?.filter((ft) => ft?._id !== val?._id),
        goodsId: prev?.goodsId?.filter((ft) => ft !== val?._id),
      }));
    } else {
      setGoodsList((prev) => ({
        ...prev,
        goods: [...prev?.goods, val],
        goodsId: [...prev?.goodsId, val?._id],
      }));
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("select_goods")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              <CreateButton
                label="next"
                onAction={onNext}
                customStyle={{
                  margin: "0",
                  padding: "0.1rem 0.5rem",
                }}
              />
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {storeAllGoodsList?.list?.map((goods, index) =>
            storeAllGoodsList?.list?.length === index + 1 ? (
              <div key={goods?._id} ref={ref}>
                <SelectReturnGoodsCard
                  goods={goods}
                  onSelectGoods={onSelectGoods}
                  selectedId={goodsList.goodsId}
                />
              </div>
            ) : (
              <SelectReturnGoodsCard
                goods={goods}
                key={goods?._id}
                onSelectGoods={onSelectGoods}
                selectedId={goodsList.goodsId}
              />
            )
          )}
          {storeAllGoodsListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeAllGoodsList?.list?.length === 0 && (
              <EmptyMessage
                title="No goods found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            storeAllGoodsList?.list?.length === 0 && (
              <EmptyMessage
                title="No goods."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectReturnGoods;
