import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../StoreManager.module.css";
const SelectReturnGoodsCard = ({ goods, onSelectGoods, selectedId }) => {
  const { t } = useTranslation();

  const onClick = () => {
    onSelectGoods(goods);
  };
  return (
    <>
      <div
        className={
          selectedId?.includes(goods?._id)
            ? `${style.smc_card} ${style.smc_card_active}`
            : style.smc_card
        }
        onClick={onClick}
      >
        <div className={style.smc_card_contatiner}>
          <img
            src={
              goods?.good_icon
                ? `${imageShowUrl}/${goods?.good_icon}`
                : `${assestsUserFinanceUrl}/category.svg`
            }
            alt="categor good_icon"
          />
          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {t("name")}
              {": "}
              {goods?.goods_name ?? ""}
            </h6>
            <p>
              {t("remaining_quantity")} {": "} {goods?.goods_volume ?? ""}
            </p>
            <p>
              {t("price_per_unit")} {": "} {goods?.goods_price ?? ""}
            </p>
          </div>
        </div>
      </div>
    </>
  );
};
export default SelectReturnGoodsCard;
