import { useState } from "react";
import ReturnGoodsStoreConfirm from "./ReturnGoodsStoreConfirm";
import SelectReturnGoods from "./SelectReturnGoods";
import IssueRecordForm from "../StoreIssue/StoreIssueRecord/IssueRecordForm";

const ReturnGoodsStore = ({ onClose, onNavigate, mid, stid, flow }) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    arr: [],
    goods: [],
    goodsId: [],
  });
  const onGoodsSelect = (data) => {
    setState((prev) => ({
      ...prev,
      goods: data?.goods,
      goodsId: data?.goodsId,
    }));
    setStep(2);
  };

  return (
    <>
      {step === 1 ? (
        <SelectReturnGoods
          onClose={onClose}
          stid={stid}
          onGoodsSelect={onGoodsSelect}
          dataState={state}
        />
      ) : step === 2 ? (
        <IssueRecordForm
          onClose={() => setStep(1)}
          state={state}
          setStep={() => setStep(3)}
          setState={setState}
        />
      ) : step === 3 ? (
        <ReturnGoodsStoreConfirm
          onClose={() => setStep(2)}
          state={state}
          onParentClose={onClose}
          stid={stid}
          onNavigate={onNavigate}
          flow={flow}
          mid={mid}
        />
      ) : null}
    </>
  );
};

export default ReturnGoodsStore;
