import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useStoreGoodsRequestStock } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import { useState } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";

const RequestGoodsStoreConfirm = ({
  onParentClose,
  state,
  onClose,
  mid,
  flow,
  onNavigate,
  stid,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [storeGoodsRequestStock] = useStoreGoodsRequestStock();
  const onNext = () => {
    if (mid && state?.arr?.length && flow && stid) {
      setDisabled((pre) => !pre);
      storeGoodsRequestStock({
        stid: stid,
        bodyParameter: {
          arr: state?.arr,
          flow: flow,
          request_by: mid,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onNavigate();
          onParentClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("confirm_to_request_stock")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom
            customStyleBorder={{
              marginBottom: "0",
            }}
          />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <CreateButton label="request_stock" onAction={onNext} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default RequestGoodsStoreConfirm;
